.page1 {
  height: fit-content;
  min-height: 100vh;
  right: 0;
  left: 0;
  overflow-x: hidden;
}

.page1-1 {
  background-color: #f8f8f8;
  height: fit-content;
  min-height: 100vh;
  padding: 0px 5%;
}

.page1-body {
  display: flex;
  justify-content: space-around;
}

.page1-text {
  margin-top: 150px;
  width: 650px;
  display: flex;
  flex-direction: column;
}

.page1-card {
  margin-top: 150px;
  height: 527px;
  width: 452px;
  display: flex;
  align-items: center;
  flex-direction: column;
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px,
    rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
  padding: 44px 32px;
  background-color: white;
}

@media (max-width: 700px) {
  .page1-text {
    margin-top: 100px;
    width: 80vw;
  }
  .page1-body {
    flex-direction: column;
  }
  .page1-2 {
    width: 90vw;
  }
  .page1-3 {
    width: 90vw;
  }
  .page1-4 {
    width: 90vw;
  }
  .page1-5 {
    width: 90vw;
  }

  .page1-card {
    width: 90vw !important;
    height: fit-content;
    margin-top: 100px;
    margin-bottom: 60px;
    padding: none;
  }
}
